<template>
  <div class="home">
    <div class="wrapper">
      <div class="columns">
        <div class="column">
          <div v-for="(genre) in genres" v-bind:key="genre">
            <Genre :Genre= "genre"/> 
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import Genre from './Genre'
export default {
  components:{
    Genre
  },
  data () { 
    return {
      genres: []
    }
  },
  mounted () {
    axios
      .get('http://ec2-34-227-26-148.compute-1.amazonaws.com:3000/genres')
      .then((res) => {
        console.log("res: ", res.data);
        this.genres = res.data;
      });
  }
}
</script>

